.no-inspectors {
	font-size: 2.25rem;
	padding: 3rem 1rem;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	line-height: 1.5;
}

.no-inspectors > * {
	max-width: 67.5rem;
}

.no-inspectors::before,
.no-inspectors::after {
	content: "";
	display: block;
	width: 90%;
	border: 1px solid #808080;
	background-color: currentColor;
	margin-bottom: 1rem;
}

.no-inspectors::after {
	margin-top: 1rem;
	margin-bottom: 0;
}
